.cards {
  padding: 1.875rem;
}
.cards .card__1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2.5rem;
  margin-bottom: 1.875rem;
  position: relative;
  color: var(--white);
  overflow: hidden;
  overflow-wrap: break-word;
}
.cards .card__1::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--modviolet);
  z-index: -1;
  border-radius: 0.7rem;
  box-shadow: 0.3125rem 0.75rem 0.75rem rgba(0, 0, 0, 0.1);
}
.cards .card__1 .container {
  display: grid;
  grid-template-columns: 0.25fr 1fr 1.5fr;
  margin-bottom: 1.25rem;
  margin-left: -0.625rem;
}
.cards .card__1 .container--Daniel {
  grid-column: 1;
}
.cards .card__1 .container--name1 {
  grid-column: 2;
}
.cards .card__1--Daniel {
  height: 2.8125rem;
  width: 2.8125rem;
  position: relative;
  margin-right: 1.25rem;
  z-index: 1;
  border-radius: 6.25rem;
  border: 0.1875rem solid var(--ltviolet);
}
.cards .card__1--name1 {
  display: flex;
  flex-direction: column;
  position: relative;
  font-size: 1.25rem;
  font-weight: 0;
  z-index: 1;
}
.cards .card__1--ver1 {
  position: relative;
  font-size: 0.9375rem;
  font-weight: 0;
  z-index: 1;
  opacity: 50%;
}
.cards .card__1--img {
  position: absolute;
  height: 29%;
  left: 50%;
  top: 50%;
  transform: translate(17%, -170%);
  width: 37%;
  z-index: -1;
}
.cards .card__1 h1,
.cards .card__1 p {
  display: flex;
  flex-direction: column;
}
