@media (min-width: 54em) {
  .cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto auto;
    gap: 1.875rem;
    row-gap: 0rem;
  }
  .cards .card__1 {
    grid-column: 1/3;
    grid-row: 1;
  }
  .cards .card__2 {
    grid-column: 3;
    grid-row: 1;
  }
  .cards .card__3 {
    grid-column: 1;
    grid-row: 2;
  }
  .cards .card__4 {
    grid-column: 2/4;
    grid-row: 2;
  }
  .cards .card__5 {
    grid-column: 4;
    grid-row: 1/3;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  font-family: "Barlow Semi Condensed", sans-serif;
  background: var(--ltgrayblue);
  height: 100vh;
  padding: 1.25rem 0.625rem 0 0.625rem;
}
body .attribution {
  font-size: 1.25rem;
  margin-top: 3.75rem;
  text-align: center;
  margin-bottom: 40px;
}
body .attribution a {
  color: hsl(228, 45%, 44%);
}

:root {
  --modviolet: hsl(263, 55%, 52%);
  --ltviolet: hsl(281, 73%, 69%);
  --vdkblblue: hsl(219, 29%, 14%);
  --vdkgrayblue: hsl(217, 19%, 35%);
  --ltgrayblue: hsl(210, 46%, 95%);
  --ltgray: hsl(0, 0%, 81%);
  --white: hsl(0, 0%, 100%);
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0.625rem;
  line-height: 1.1;
  overflow-wrap: break-word;
}

h1 {
  font-weight: 600;
}

h2 {
  font-weight: 600;
}

a,
a:visited,
a:hover {
  cursor: pointer;
}

p {
  font-weight: 500;
  line-height: 1.5;
  font-size: 0.8125rem;
  opacity: 70%;
  overflow-wrap: break-word;
  margin-bottom: -0.625rem;
}
@media (min-width: 54em) {
  p {
    margin-bottom: -1.875rem;
  }
}